.counter {
  align-self: flex-start;
  z-index: 10;
  margin-top: -40px;
  margin-bottom: 55px;
  .totalCount {
    font-family: "PT Sans";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 18px;
    color: #42567a;
    margin-bottom: 20px;
  }
  .btns {
    display: flex;
    gap: 20px;
  }
  .leftBtn,
  .rightBtn {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1px solid rgba(66, 86, 122, 0.5);
    background-color: transparent;
  }
  .leftBtn {
    background: url("../../../assets/buttonLeft.png") 0 0 / cover no-repeat;
  }
  .rightBtn {
    background: url("../../../assets/buttonRight.png") 0 0 / cover no-repeat;
  }
  .leftBtn:disabled,
  .rightBtn:disabled {
    opacity: 0.5;
  }
  @media (max-width: 320px) {
    position: absolute;
    bottom: -40px;
    margin-top: 0;
    .totalCount {
      margin-bottom: 10px;
    }
    .btns {
      gap: 8;
    }
    .leftBtn,
    .rightBtn {
      width: 20px;
      height: 20px;
    }
  }
}
